.initialize-habits {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
}

.init-card {
  background: #242424;
  border-radius: 12px;
  padding: 32px;
  width: 90%;
  max-width: 500px;
  border: 1px solid #333;
}

.init-card h2 {
  font-size: 24px;
  font-weight: 500;
  color: #e0e0e0;
  margin: 0 0 16px 0;
}

.init-card p {
  color: #94a3b8;
  margin: 0 0 12px 0;
  font-size: 14px;
}

.habit-count {
  font-size: 16px;
  color: #60a5fa;
  font-weight: 500;
  margin: 16px 0;
}

.categories-preview {
  background: #1a1a1a;
  border-radius: 8px;
  padding: 16px;
  margin: 20px 0;
}

.category-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 8px 0;
  color: #e0e0e0;
  font-size: 14px;
}

.category-dot {
  width: 12px;
  height: 12px;
  border-radius: 2px;
  flex-shrink: 0;
}

.progress-bar {
  position: relative;
  height: 24px;
  background: #1a1a1a;
  border-radius: 12px;
  overflow: hidden;
  margin: 20px 0;
}

.progress-fill {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background: linear-gradient(90deg, #60a5fa, #a855f7);
  transition: width 0.3s ease;
}

.progress-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #e0e0e0;
  font-size: 12px;
  font-weight: 500;
}

.init-actions {
  display: flex;
  gap: 12px;
  justify-content: flex-end;
  margin-top: 24px;
}

.btn-skip,
.btn-initialize {
  padding: 10px 20px;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  border: none;
}

.btn-skip {
  background: #2a2a2a;
  color: #94a3b8;
  border: 1px solid #333;
}

.btn-skip:hover:not(:disabled) {
  background: #333;
  color: #e0e0e0;
}

.btn-initialize {
  background: #60a5fa;
  color: #1a1a1a;
}

.btn-initialize:hover:not(:disabled) {
  background: #3b82f6;
}

.btn-skip:disabled,
.btn-initialize:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}